.container {
  width: 50%;
  padding: 0.2rem;
}

.list {
  width: 50%;
}

.grid {
  width: 100%;
}

.video {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  /* box-shadow: 3px 3px 5px 0px rgba(191, 191, 191, 0.53); */
  cursor: pointer;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.3),
    rgba(255, 255, 255, 0.05)
  );
  border-radius: 0.2rem;
  backdrop-filter: blur(2rem);
  transition: transform 250ms ease-in;
}

.video:hover {
  transform: scale(1.02);
}

.thumbnail {
  width: 40%;
  height: 100%;
}

.metadata {
  margin-left: 0.2rem;
}

.title,
.channel {
  margin: 0;
  font-size: 0.9rem;
}

.channel {
  font-size: 0.6rem;
}

@media screen and (max-width: 38rem) {
  .container {
    width: 100%;
  }
}
